/**
 * Alert <uo-tab>
 *
 * Attributes:
 * id        The ID of the tab. This is used to get the tab panel.
 * active    Defines whether the tab is active or not
 * disabled  Defines whether the tab is disabled or not
 * loading   Defines whether the tab is loading or not
 * direction Row or column, as with flex
 * status    "warning", "error", "success"
 * href      Link the tab should go to, if the content is not loaded
 */

// Import style for web component
const styles = require( './web-component-style.scss' );

// Alert template
const template = document.createElement( 'template' );
template.innerHTML = `
    <style>

        ${ styles }

    </style>

    <div class="tab">
    	<a>
    		<slot></slot>
    	</a>
    </div>
`;

/**
 * Creates <uo-tab>
 */
class AutomatorTab extends HTMLElement {
	/**
	 * Defines observed attributes
	 *
	 * @return {Array} Observed attributes
	 */
	static get observedAttributes() {
		return [ 'active', 'disabled', 'loading', 'id', 'direction', 'status', 'href' ];
	}

	/**
	 * Creates the component
	 *
	 * @return {undefined}
	 */
	constructor() {
		super();

		// Attach shadow
		this.attachShadow( {
			mode: 'open',
		} );

		// Create alert
		this.shadowRoot.appendChild( template.content.cloneNode( true ) );
	}

	/**
	 * Callback invoked when the attribute changes
	 *
	 * @param {String} attrName Attribute name
	 * @param {String} oldValue The old value
	 * @param {String} newValue The new value
	 * @return {undefined}
	 */
	attributeChangedCallback( attrName, oldValue, newValue ) {
		// Check if the old value is different than the new one
		// This will prevent an infinite loop
		if ( oldValue !== newValue ) {
			// Check if it's a boolean attribute
			if ( [ 'active', 'disabled', 'loading' ].includes( attrName ) ) {
				this[ attrName ] = this.hasAttribute( attrName );
			} else {
				this[ attrName ] = this.getAttribute( attrName );
			}
		}
	}

	/**
	 * Returns the id of the tab
	 *
	 * @return {String} The id
	 */
	get id() {
		return this.getAttribute( 'id' );
	}

	/**
	 * Sets the id of the tab
	 *
	 * @return {undefined}
	 */
	set id( value ) {
		// Check if the value exists
		if ( value ) {
			// Update the attribute
			this.setAttribute( 'id', value );
		} else {
			// Remove the attribute
			this.removeAttribute( 'id' );
		}
	}

	/**
	 * Determines if the tab is active by checking if the element has
	 * the attribute "active"
	 *
	 * @return {Boolean} True if it's active
	 */
	get active() {
		return this.hasAttribute( 'active' );
	}

	/**
	 * Sets the status of the active attribute
	 *
	 * @param {Boolean} value True if the tab should be active
	 * @return {undefined}
	 */
	set active( value ) {
		if ( value ) {
			// Set active status in the host
			this.setAttribute( 'active', '' );

			// Set active attribute in the tab inside
			this.$tab.setAttribute( 'active', '' );
		} else {
			// Set active status in the host
			this.removeAttribute( 'active' );

			// Set active attribute in the tab inside
			this.$tab.removeAttribute( 'active' );
		}
	}

	/**
	 * Determines if the tab is disabled by checking if the element has
	 * the attribute "disabled"
	 *
	 * @return {Boolean} True if it's disabled
	 */
	get disabled() {
		return this.hasAttribute( 'disabled' );
	}

	/**
	 * Sets the status of the disabled attribute
	 *
	 * @param {Boolean} value True if the tab should be disabled
	 * @return {undefined}
	 */
	set disabled( value ) {
		if ( value ) {
			// Set disabled status in the host
			this.setAttribute( 'disabled', '' );

			// Set disabled attribute in the tab inside
			this.$tab.setAttribute( 'disabled', '' );
		} else {
			// Set disabled status in the host
			this.removeAttribute( 'disabled' );

			// Set disabled attribute in the tab inside
			this.$tab.removeAttribute( 'disabled' );
		}
	}

	/**
	 * Determines if the tab is loading by checking if the element has
	 * the attribute "loading"
	 *
	 * @return {Boolean} True if it's active
	 */
	get loading() {
		return this.hasAttribute( 'loading' );
	}

	/**
	 * Sets the status of the loading attribute
	 *
	 * @param {Boolean} value True if the tab should be loading
	 * @return {undefined}
	 */
	set loading( value ) {
		if ( value ) {
			// Set loading status in the host
			this.setAttribute( 'loading', '' );

			// Set loading attribute in the tab inside
			this.$tab.setAttribute( 'loading', '' );
		} else {
			// Set loading status in the host
			this.removeAttribute( 'loading' );

			// Set loading attribute in the tab inside
			this.$tab.removeAttribute( 'loading' );
		}
	}

	/**
	 * Returns the status of the tab
	 *
	 * @return {String} The status
	 */
	get status() {
		return this.getAttribute( 'status' );
	}

	/**
	 * Sets the status of the tab
	 *
	 * @return {undefined}
	 */
	set status( value ) {
		// Check if the value exists
		if ( [ 'warning', 'error', 'success' ].includes( value ) ) {
			// Update the attribute
			this.setAttribute( 'status', value );

			// Set attribute in the tab element
			this.$tab.setAttribute( 'status', value );
		} else {
			// Remove the attribute
			this.removeAttribute( 'status' );

			// Remove attribute from the tab element
			this.$tab.removeAttribute( 'status' );
		}
	}

	/**
	 * Returns the direction of the tab
	 *
	 * @return {String} The direction
	 */
	get direction() {
		return this.getAttribute( 'direction' );
	}

	/**
	 * Sets the direction of the tab
	 *
	 * @return {undefined}
	 */
	set direction( value ) {
		// Check if the value exists
		if ( value ) {
			// Update the attribute
			this.setAttribute( 'direction', value );

			// Update the attribute also in the .tabs element
			this.$tab.setAttribute( 'direction', value );
		} else {
			// Remove the attribute
			this.removeAttribute( 'direction' );

			// Remove the attribute also in the .tabs element
			this.$tab.removeAttribute( 'direction' );
		}
	}

	/**
	 * Returns the href of the tab
	 *
	 * @return {String} The href
	 */
	get href() {
		return this.getAttribute( 'href' );
	}

	/**
	 * Sets the href of the tab
	 *
	 * @return {undefined}
	 */
	set href( value ) {
		// Check if the value exists
		if ( value ) {
			// Update the attribute
			this.setAttribute( 'href', value );
		} else {
			// Remove the attribute
			this.removeAttribute( 'href' );
		}
	}

	/**
	 * Returns the tab element
	 *
	 * @return {Node} The tab
	 */
	get $tab() {
		return this.shadowRoot.querySelector( '.tab' );
	}
}

customElements.define( 'uo-tab', AutomatorTab );
