/**
 * Alert <uo-alert>
 *
 * Attributes:
 * heading      The heading of the alert
 * type         Type of the alert. "warning", "error", "success", "info"
 */

// Import utilities
import isEmpty from '@automator/utilities/is-empty';

// Import style for web component
const styles = require( './web-component-style.scss' );

// Alert template
const template = document.createElement( 'template' );
template.innerHTML = `
    <style>

        :host {
            display: block;
        }
        
        ${ styles }

    </style>

    <div class="alert">
        <div class="alert-icon"></div>
        <div class="alert-content">
            <slot></slot>
        </div>
    </div>
`;

class AutomatorAlert extends HTMLElement {
	/**
	 * Defines observed attributes
	 *
	 * @return {Array} Observed attributes
	 */
	static get observedAttributes() {
		return [ 'heading', 'type', 'no-radius' ];
	}

	/**
	 * Creates the component
	 *
	 * @return {undefined}
	 */
	constructor() {
		super();

		// Attach shadow
		this.attachShadow( {
			mode: 'open',
		} );

		// Create alert
		this.shadowRoot.appendChild( template.content.cloneNode( true ) );
	}

	/**
	 * Updates the component
	 *
	 * @return {undefined}
	 */
	_update() {
		/**
		 * Heading
		 */

		// Add heading
		if ( ! isEmpty( this.heading ) ) {
			// Create the heading node
			const $heading = document.createElement( 'div' );
			$heading.classList.add( 'alert-heading' );

			// Add heading
			$heading.innerText = this.heading;

			// Add it to the alert
			this.shadowRoot.querySelector( '.alert' ).appendChild( $heading );
		} else {
			// Check if it has the element heading and remove it
			const $heading = this.shadowRoot.querySelector( '.alert-heading' );

			if ( $heading ) {
				this.shadowRoot.removeChild( $heading );
			}
		}
	}

	/**
	 * Callback invoked when the component is added to the DOM
	 *
	 * @return {undefined}
	 */
	connectedCallback() {}

	/**
	 * Callback invoked when the attribute changes
	 *
	 * @param {String} attrName Attribute name
	 * @param {String} oldValue The old value
	 * @param {String} newValue The new value
	 * @return {undefined}
	 */
	attributeChangedCallback( attrName, oldValue, newValue ) {
		// Check if the old value is different than the new one
		// This will prevent an infinite loop
		if ( oldValue !== newValue ) {
			// Check if it's the "no-radius" attribute
			if ( attrName == 'no-radius' ) {
				this.radius = ! this.hasAttribute( 'no-radius' );
			} else {
				this[ attrName ] = this.getAttribute( attrName );
			}
		}
	}

	/**
	 * Returns the heading of the alert
	 *
	 * @return {String} The heading
	 */
	get heading() {
		return this.getAttribute( 'heading' );
	}

	/**
	 * Sets the heading of the alert
	 *
	 * @return {undefined}
	 */
	set heading( value ) {
		// Check if the value exists
		if ( value ) {
			// Update the attribute
			this.setAttribute( 'heading', value );
		} else {
			// Remove the attribute
			this.removeAttribute( 'heading' );
		}

		// Update the UI
		this._update();
	}

	/**
	 * Returns the status of the radius of the alert
	 *
	 * @return {Boolean} True if the alert has radius
	 */
	get radius() {
		return ! this.hasAttribute( 'no-radius' );
	}

	/**
	 * Sets the radius status of the alert
	 *
	 * @return {undefined}
	 */
	set radius( value ) {
		// Check if the value exists
		if ( value ) {
			// Remove the attribute
			this.removeAttribute( 'no-radius' );

			// Remove attribute from the alert element
			this.$alert.removeAttribute( 'no-radius' );
		} else {
			// Update the attribute
			this.setAttribute( 'no-radius', '' );

			// Set attribute in the alert element
			this.$alert.setAttribute( 'no-radius', '' );
		}
	}

	/**
	 * Returns the type of the alert
	 *
	 * @return {String} The type
	 */
	get type() {
		return this.getAttribute( 'type' );
	}

	/**
	 * Sets the type of the alert
	 *
	 * @return {undefined}
	 */
	set type( value ) {
		// Check if the value exists
		if ( [ 'warning', 'error', 'success', 'info' ].includes( value ) ) {
			// Update the attribute
			this.setAttribute( 'type', value );

			// Set attribute in the alert element
			this.$alert.setAttribute( 'type', value );
		} else {
			// Remove the attribute
			this.removeAttribute( 'type' );

			// Remove attribute from the alert element
			this.$alert.removeAttribute( 'type' );
		}
	}

	/**
	 * Returns the alert element
	 *
	 * @return {Node} The alert
	 */
	get $alert() {
		return this.shadowRoot.querySelector( '.alert' );
	}
}

customElements.define( 'uo-alert', AutomatorAlert );
