/**
 * Button <uo-button>
 *
 * Attributes:
 * disabled  Disables the button.
 * loading   Adds loading animation to the button. Disables the button.
 * color     Color of the button. "secondary", "danger".
 * size      Size of the button. "small", "extra-small".
 * href      The hyperlink of the button.
 * target    The target of the button.
 */

// Import utilities
import isEmpty from '@automator/utilities/is-empty';

// Import style for web component
const styles = require( './web-component-style.scss' );

/**
 * Creates the <uo-button> web component
 *
 * Properties:
 * {Boolean} disabled
 */
class AutomatorButton extends HTMLElement {
	/**
	 * Defines observed attributes
	 *
	 * @return {Array} Observed attributes
	 */
	static get observedAttributes() {
		return [ 'color', 'size', 'disabled', 'loading', 'href', 'target', 'type' ];
	}

	/**
	 * Renders the component
	 *
	 * @return {undefined}
	 */
	constructor() {
		super();

		// Attach shadow DOM
		this.attachShadow( {
			mode: 'open',
		} );

		// Create button
		this.shadowRoot.innerHTML = `
			<style>
				${ styles }
			</style>

			<button>
				<slot></slot>
			</button>
		`;
	}

	/**
	 * Callback invoked when the component is added to the DOM
	 *
	 * @return {undefined}
	 */
	connectedCallback() {
		// Listen clicks
		this.addEventListener( 'click', this._handleClick );
	}

	/**
	 * Callback invoked when the component is removed from the DOM
	 *
	 * @return {undefined}
	 */
	disconnectedCallback() {
		// Stop listening clicks
		this.removeEventListener( 'click', this._handleClick );
	}

	/**
	 * Callback invoked when the attribute changes
	 *
	 * @param {String} attrName Attribute name
	 * @param {String} oldValue The old value
	 * @param {String} newValue The new value
	 * @return {undefined}
	 */
	attributeChangedCallback( attrName, oldValue, newValue ) {
		// Check if the old value is different than the new one
		// This will prevent an infinite loop
		if ( oldValue !== newValue ) {
			// Check if it's a boolean attribute
			if ( [ 'loading', 'disabled' ].includes( attrName ) ) {
				this[ attrName ] = this.hasAttribute( attrName );
			} else {
				this[ attrName ] = this.getAttribute( attrName );
			}
		}
	}

	/**
	 * Handles clicks made to the component
	 *
	 * @param  event
	 * @return {undefined}
	 */
	_handleClick( event ) {
		// Check if it's disabled and prevent the event
		if ( this.disabled ) {
			event.preventDefault();
			event.stopPropagation();
		}

		// Make sure the button is not disabled
		if ( ! this.disabled ) {
			// Check if it has a hyperlink defined
			if ( ! isEmpty( this.href ) ) {
				// The user added a link, we have to send him there
				// Simulate link
				const anchor = document.createElement( 'a' );

				// Set hyperlink
				anchor.setAttribute( 'href', this.href );

				// Check if it has a target defined
				if ( ! isEmpty( this.target ) ) {
					// Add target
					anchor.setAttribute( 'target', this.target );
				}

				// Simulate click
				anchor.click();
			}

			// Check if it has a type defined
			if ( ! isEmpty( this.type ) ) {
				// Check if the user wants to submit a form
				if ( this.type == 'submit' ) {
					// Get the form
					const $form = this.closest( 'form' );

					// Create a fake submit button
					const $submitButton = document.createElement( 'button' );
					$submitButton.setAttribute( 'type', 'submit' );

					// Add it to the form
					$form.appendChild( $submitButton );

					// Trigger a click
					$submitButton.click();

					// Remove the button
					$form.removeChild( $submitButton );
				}
			}
		}
	}

	/**
	 * Determines if the button is disabled by checking if the element has
	 * the attribute "disabled"
	 *
	 * @return {Boolean} True if it's disabled
	 */
	get disabled() {
		return this.hasAttribute( 'disabled' );
	}

	/**
	 * Sets the status of the disabled attribute
	 *
	 * @param {Boolean} value True if the button should be disabled
	 * @return {undefined}
	 */
	set disabled( value ) {
		if ( value ) {
			// Set disabled status in the host
			this.setAttribute( 'disabled', '' );

			// Set disabled attribute in the button inside
			this.$button.setAttribute( 'disabled', '' );
		} else {
			// Set disabled status in the host
			this.removeAttribute( 'disabled' );

			// Set disabled attribute in the button inside
			this.$button.removeAttribute( 'disabled' );
		}
	}

	/**
	 * Determines if the button is loading by checking if the element has
	 * the attribute "loading"
	 *
	 * @return {Boolean} True if it's loading
	 */
	get loading() {
		return this.hasAttribute( 'loading' );
	}

	/**
	 * Sets the status of the loading attribute
	 *
	 * @param {Boolean} value True if the button is loading
	 * @return {undefined}
	 */
	set loading( value ) {
		if ( value ) {
			// Set loading status in the host
			this.setAttribute( 'loading', '' );

			// Set loading class to the button inside
			this.$button.setAttribute( 'loading', '' );

			// Disable the button
			this.disabled = true;
		} else {
			// Set loading status in the host
			this.removeAttribute( 'loading' );

			// Remove loading class in the button inside
			this.$button.removeAttribute( 'loading' );

			// Enable the button
			this.disabled = false;
		}
	}

	/**
	 * Determines the size of the button
	 *
	 * @return {String} The size of the button
	 */
	get size() {
		return this.getAttribute( 'size' );
	}

	/**
	 * Sets the size of the button
	 *
	 * @param {Boolean} value The size: Either "small" or "extra-small"
	 * @return {undefined}
	 */
	set size( value ) {
		// Check if the value of the attribute is valid
		if ( [ 'small', 'extra-small' ].includes( value ) ) {
			// Set size in the host
			this.setAttribute( 'size', value );

			// Set size in button
			this.$button.setAttribute( 'size', value );
		} else {
			// Set size in the host
			this.removeAttribute( 'size' );

			// Remove size from button
			this.$button.removeAttribute( 'size' );

			// Check if debugging mode is enabled
			// Check if the value is not empty
			// eslint-disable-next-line
			if ( UncannyAutomatorBackend.debugging.enabled && ! isEmpty( value ) ) {
				// eslint-disable-next-line no-console
				console.warn( `uo-button: Invalid size (${ value }). Try with: "small" or "extra-small"` );
			}
		}
	}

	/**
	 * Determines the color of the button
	 *
	 * @return {String} The color of the button
	 */
	get color() {
		return this.getAttribute( 'color' );
	}

	/**
	 * Sets the color of the button
	 *
	 * @param {Boolean} value The color: Either "secondary" or "danger"
	 * @return {undefined}
	 */
	set color( value ) {
		// Check if the value of the attribute is valid
		if ( [ 'secondary', 'danger' ].includes( value ) ) {
			// Set color in the host
			this.setAttribute( 'color', value );

			// Set color in button
			this.$button.setAttribute( 'color', value );
		} else {
			// Set color in the host
			this.removeAttribute( 'color' );

			// Remove color from button
			this.$button.removeAttribute( 'color' );

			// Check if debugging mode is enabled
			// Check if the value is not empty
			// eslint-disable-next-line
			if ( UncannyAutomatorBackend.debugging.enabled && ! isEmpty( value ) ) {
				// eslint-disable-next-line no-console
				console.warn( `uo-button: Invalid color (${ value }). Try with: "secondary" or "danger"` );
			}
		}
	}

	/**
	 * Returns the current hyperlink
	 *
	 * @return {String} The href of the button
	 */
	get href() {
		return this.getAttribute( 'href' );
	}

	/**
	 * Sets the hyperlink of the button
	 *
	 * @param {Boolean} value The hyperlink
	 * @return {undefined}
	 */
	set href( value ) {
		// Check if the href value is defined
		if ( value ) {
			// Set hyperlink in the host
			this.setAttribute( 'href', value );
		} else {
			// Set hyperlink in the host
			this.removeAttribute( 'href' );
		}
	}

	/**
	 * Returns the current hyperlink target
	 *
	 * @return {String} The hyperlink target
	 */
	get target() {
		return this.getAttribute( 'target' );
	}

	/**
	 * Sets the hyperlink target of the button
	 *
	 * @param {Boolean} value The hyperlink target
	 * @return {undefined}
	 */
	set target( value ) {
		// Check if the target value is defined
		if ( value ) {
			// Set hyperlink in the host
			this.setAttribute( 'target', value );
		} else {
			// Set hyperlink in the host
			this.removeAttribute( 'target' );
		}
	}

	/**
	 * Returns the current type
	 *
	 * @return {String} The button type
	 */
	get type() {
		return this.getAttribute( 'type' );
	}

	/**
	 * Sets the type of the button
	 *
	 * @param {Boolean} value The button
	 * @return {undefined}
	 */
	set type( value ) {
		// Check if the target value is defined
		if ( value ) {
			// Set hyperlink in the host
			this.setAttribute( 'type', value );
		} else {
			// Set hyperlink in the host
			this.removeAttribute( 'type' );
		}
	}

	/**
	 * Returns the button element
	 *
	 * @return {Node} The button
	 */
	get $button() {
		return this.shadowRoot.querySelector( 'button' );
	}
}

customElements.define( 'uo-button', AutomatorButton );
