nsition: opacity 150ms ease-in-out;\n}","/**\n * Dropdown: <uo-dropdown>\n * Figma: <Bubble>\n *\n * Overridable styles\n * This has to be used as a last resort. Try to use the default styles.\n *\n * Colors:\n * --uap-dropdown-style-width String  The width\n * --uap-dropdown-z-index     Integer The z-index\n */\n\n// Import icon mixin\n@import \"@automator/components/icon/mixin.scss\";\n\n// Import animations\n// We have to do this because we're inside a web component\n@import \"@automator/animations/fade-in/style.scss\";\n@import \"@automator/animations/fade-out/style.scss\";\n\n// Variables\n$space_between_button_and_bubble: 15px;\n$default_z_index: 1000;\n\n.dropdown {\n\t$dropdown: &;\n\n\tposition: relative;\n\n\t&-button {\n\t\tcursor: pointer;\n\t}\n\n\t&-bubble {\n\t\t// Hidden by default\n\t\tdisplay: none;\n\n\t\tposition: absolute;\n\n\t\twidth: var(--uap-dropdown-style-width, auto);\n\n\t\tmin-width: 100px;\n\t\tmax-width: 200px;\n\n\t\tz-index: var(--uap-dropdown-z-index, $default_z_index);\n\n\t\tbackground: #fff;\n\n\t\tfont-size: var(--uap-font-size--body-small);\n\t\tline-height: var(--uap-line-height--body-small);\n\n\t\tborder-radius: var(--uap-border-radius);\n\t\tbox-shadow: var(--uap-shadow-dropdown);\n\n\t\tborder: 1px solid var(--uap-dropdown-border-color);\n\n\t\ttext-align: left;\n\t\tfont-weight: 400;\n\n\t\t// Don't allow the user to select the text inside\n\t\t// This prevents the browser from automatically selecting the content\n\t\t// when the user clicks on the toggle button too fast\n\t\tuser-select: none;\n\n\t\t// Position\n\t\ttop: calc( 100% + $space_between_button_and_bubble );\n\n\t\t// Caret\n\t\t&::before,\n\t\t&::after {\n\t\t\tcontent: \"\";\n\n\t\t\tborder: var(--uap-dropdown-caret-size) solid transparent;\n\n\t\t\tposition: absolute;\n\t\t\tbottom: 100%;\n\t\t}\n\n\t\t// Gray part to simulate border\n\t\t&::before {\n\t\t\tborder-width: calc( var(--uap-dropdown-caret-size) + 1px );\n\t\t\tborder-bottom-color: var(--uap-dropdown-border-color);\n\n\t\t\t// We want this below the after\n\t\t\tz-index: calc( var(--uap-dropdown-z-index, $default_z_index) + 1 );\n\t\t}\n\n\t\t// White part\n\t\t&::after {\n\t\t\tborder-bottom-color: #fff;\n\n\t\t\t// We want this above the before\n\t\t\tz-index: calc( var(--uap-dropdown-z-index, $default_z_index) + 2 );\n\t\t}\n\n\t\t/**\n\t\t * List/options\n\t\t */\n\t\t\n\t\tul {\n\t\t\tlist-style: none;\n\t\t\tmargin: 0;\n\t\t\tpadding: 0;\n\n\t\t\tli {\n\t\t\t\t$li-padding-left: 10px;\n\t\t\t\t$li-padding-top: 5px;\n\n\t\t\t\tlist-style: none;\n\t\t\t\tmargin: 0;\n\t\t\t\tpadding: 0;\n\n\t\t\t\tcursor: pointer;\n\n\t\t\t\t&:not(:last-child) {\n\t\t\t\t\tborder-bottom: 1px solid var(--uap-border-color-gray);\n\t\t\t\t}\n\n\t\t\t\tpadding: $li-padding-top $li-padding-left;\n\n\t\t\t\t// If it has an anchor inside, remove the padding of the li\n\t\t\t\t> a:only-child {\n\t\t\t\t\tdisplay: block;\n\t\t\t\t\tmargin: ( $li-padding-top * -1 ) ( $li-padding-left * -1 );\n\t\t\t\t\tpadding: $li-padding-top $li-padding-left;\n\t\t\t\t}\n\n\t\t\t\t/**\n\t\t\t\t * Disabled li\n\t\t\t\t */\n\t\t\t\t\n\t\t\t\t&[disabled],\n\t\t\t\t&[loading] {\n\t\t\t\t\tcursor: default;\n\t\t\t\t\tpointer-events: none;\n\t\t\t\t\topacity: .6;\n\t\t\t\t}\n\n\t\t\t\t/**\n\t\t\t\t * Loading li\n\t\t\t\t */\n\t\t\t\t\n\t\t\t\t&[loading] {\n\t\t\t\t\t&::after {\n\t\t\t\t\t\t@include icon-loading;\n\n\t\t\t\t\t\ttop: 50%;\n\t\t\t\t\t\tright: 0;\n\t\t\t\t\t\tleft: auto;\n\t\t\t\t\t}\n\t\t\t\t}\n\t\t\t}\n\t\t}\n\t}\n\n\t/**\n\t * ================ ALIGNMENT ================\n\t */\n\t\n\t/**\n\t * Left (Default)\n\t */\n\t\n\t&:not([align]) {\n\t\t#{ $dropdown }-bubble {\n\t\t\tleft: -10px;\n\n\t\t\t// Gray part to simulate border\n\t\t\t&::before {\n\t\t\t\tleft: 9px;\n\t\t\t}\n\n\t\t\t// White part\n\t\t\t&::after {\n\t\t\t\tleft: 10px;\n\t\t\t}\n\t\t}\n\t}\n\n\t/**\n\t * Center\n\t */\n\t\n\t&[align=center] {\n\t\t#{ $dropdown }-bubble {\n\t\t\tleft: 50%;\n\t\t\ttransform: translateX(