/**
 * Alert <uo-tab-panel>
 *
 * Attributes:
 * id        The ID of the panel
 * active    Defines whether the tab is active or not
 */

// Import style for web component
const styles = require( './web-component-style.scss' );

// Alert template
const template = document.createElement( 'template' );
template.innerHTML = `
    <style>
        
        ${ styles }

    </style>

    <div class="panel">
    	<slot></slot>
    </div>
`;

/**
 * Creates <uo-tab-panel>
 */
class AutomatorTabPanel extends HTMLElement {
	/**
	 * Defines observed attributes
	 *
	 * @return {Array} Observed attributes
	 */
	static get observedAttributes() {
		return [ 'active', 'id' ];
	}

	/**
	 * Creates the component
	 *
	 * @return {undefined}
	 */
	constructor() {
		super();

		// Attach shadow
		this.attachShadow( {
			mode: 'open',
		} );

		// Create alert
		this.shadowRoot.appendChild( template.content.cloneNode( true ) );
	}

	/**
	 * Callback invoked when the attribute changes
	 *
	 * @param {String} attrName Attribute name
	 * @param {String} oldValue The old value
	 * @param {String} newValue The new value
	 * @return {undefined}
	 */
	attributeChangedCallback( attrName, oldValue, newValue ) {
		// Check if the old value is different than the new one
		// This will prevent an infinite loop
		if ( oldValue !== newValue ) {
			// Check if it's a boolean attribute
			if ( [ 'active' ].includes( attrName ) ) {
				this[ attrName ] = this.hasAttribute( attrName );
			} else {
				this[ attrName ] = this.getAttribute( attrName );
			}
		}
	}

	/**
	 * Returns the id of the tab panel
	 *
	 * @return {String} The id
	 */
	get id() {
		return this.getAttribute( 'id' );
	}

	/**
	 * Sets the id of the tab panel
	 *
	 * @return {undefined}
	 */
	set id( value ) {
		// Check if the value exists
		if ( value ) {
			// Update the attribute
			this.setAttribute( 'id', value );
		} else {
			// Remove the attribute
			this.removeAttribute( 'id' );
		}
	}

	/**
	 * Determines if the tab panel is active by checking if the element has
	 * the attribute "active"
	 *
	 * @return {Boolean} True if it's active
	 */
	get active() {
		return this.hasAttribute( 'active' );
	}

	/**
	 * Sets the status of the active attribute
	 *
	 * @param {Boolean} value True if the tab panel should be active
	 * @return {undefined}
	 */
	set active( value ) {
		if ( value ) {
			// Set active status in the host
			this.setAttribute( 'active', '' );

			// Set active attribute in the tab panel inside
			this.$panel.setAttribute( 'active', '' );
		} else {
			// Set active status in the host
			this.removeAttribute( 'active' );

			// Set active attribute in the tab panel inside
			this.$panel.removeAttribute( 'active' );
		}
	}

	/**
	 * Returns the panel element
	 *
	 * @return {Node} The panel
	 */
	get $panel() {
		return this.shadowRoot.querySelector( '.panel' );
	}
}

customElements.define( 'uo-tab-panel', AutomatorTabPanel );
