\n\t\t\tcontent: var( --uap-icon-info-circle );\n\t\t}\n\t}\n\n\t/**\n\t * Type\n\t * Figma: <Alert> Type: Warning\n\t */\n\t\n\t&[type=warning] {\n\t\tbackground: var( --uap-alert-warning-background );\n\n\t\t#{ $alert }-icon {\n\t\t\t&:before {\n\t\t\t\tcontent: var( --uap-icon-exclamation-circle );\n\t\t\t}\n\t\t}\n\t}\n\n\t/**\n\t * Type\n\t * Figma: <Alert> Type: Error\n\t */\n\t\n\t&[type=error] {\n\t\tbackground: var( --uap-alert-error-background );\n\n\t\t#{ $alert }-icon {\n\t\t\t&:before {\n\t\t\t\tcontent: var( --uap-icon-exclamation-circle );\n\t\t\t}\n\t\t}\n\t}\n\n\t/**\n\t * Type\n\t * Figma: <Alert> Type: Success\n\t */\n\t\n\t&[type=success] {\n\t\tbackground: var( --uap-alert-success-background );\n\n\t\t#{ $alert }-icon {\n\t\t\t&:before {\n\t\t\t\tcontent: var( --uap-icon-check-circle );\n\t\t\t}\n\t\t}\n\t}\n\n\t/**\n\t * Type\n\t * Figma: <Alert> Type: Info\n\t */\n\t\n\t&[type=info] {\n\t\tbackground: var( --uap-alert-info-background );\n\n\t\t#{ $alert }-icon {\n\t\t\t&:before {\n\t\t\t\tcontent: var( --uap-icon-info-circle );\n\t\t\t}\n\t\t}\n\t}\n\n\t/**\n\t * ================ RADIUS ================\n\t */\n\t\n\t/**\n\t * No radius\n\t */\n\t\n\t&[no-radius] {\n\t\tborder-radius: 0;\n\t}\n}","/**\n * This file only contains SCSS mixins to use an icon as pseudo-element\n * Don't use the unicode to add the icon, use the CSS variables, which\n * are defined in style.scss\n */\n\n@mixin icon {\n\tfont-size: 1em;\n\tfont-family: \"Uncanny Automator Icons\" !important;\n\tspeak: none;\n\tfont-style: normal;\n\tfont-weight: normal;\n\tfont-variant: normal;\n\ttext-transform: none;\n\tline-height: 1;\n\t-webkit-font-smoothing: antialiased;\n\t-moz-osx-font-smoothing: grayscale;\n}\n\n@mixin icon-loading( $translate: translate(-50%, -50%) ) {\n\t// Define the name of the keyframes animation\n\t$keyframes_name: unique_id() + \"-rotate\";\n\n\t// Add icon\n\t@include icon;\n\tcontent: var(--uap-icon-loading);\n\n\t// Style it\n\tcolor: var(--uap-fon