// Import <uo-select-option>
import './option';

// Import utilities
import isDefined from '@automator/utilities/is-defined';
import isEmpty from '@automator/utilities/is-empty';

// Import style for web component
const styles = require( './web-component-style.scss' );

// Base template
const template = document.createElement( 'template' );
template.innerHTML = `
    <style>

        :host {
            display: inline-flex;
        }
        
        ${ styles }

    </style>

    <div class="select">
    	<div class="select-button"></div>
    	<div class="select-options">
    		<slot></slot>
    	</div>
    </div>
`;

/**
 * Select <uo-select>
 *
 * Attributes:
 */
class AutomatorSelect extends HTMLElement {
	/**
	 * Defines observed attributes
	 *
	 * @return {Array} Observed attributes
	 */
	static get observedAttributes() {
		return [ 'open' ];
	}

	/**
	 * Creates the component
	 *
	 * @return {undefined}
	 */
	constructor() {
		super();

		// Attach shadow
		this.attachShadow( {
			mode: 'open',
		} );

		// Create alert
		this.shadowRoot.appendChild( template.content.cloneNode( true ) );
	}

	/**
	 * Callback invoked when the component is added to the DOM
	 *
	 * @return {undefined}
	 */
	connectedCallback() {
		// Render the selected value
		this.#renderSelectedOption();

		// Listen clicks to the button
		this.#listenClicksOnButton();

		// Listen clicks on the options
		this.#listenClicksOnOptions();
	}

	/**
	 * Callback invoked when the attribute changes
	 *
	 * @param {String} attrName Attribute name
	 * @param {String} oldValue The old value
	 * @param {String} newValue The new value
	 * @return {undefined}
	 */
	attributeChangedCallback( attrName, oldValue, newValue ) {
		// Check if the old value is different than the new one
		// This will prevent an infinite loop
		if ( oldValue !== newValue ) {
			// Check if it's a boolean attribute
			if ( [ 'open' ].includes( attrName ) ) {
				this[ attrName ] = this.hasAttribute( attrName );
			} else {
				this[ attrName ] = this.getAttribute( attrName );
			}
		}
	}

	/**
	 * Listens clicks on the select button
	 *
	 * @return {undefined}
	 */
	#listenClicksOnButton() {
		// Listen clicks to the button
		this.$button.addEventListener( 'click', () => {
			// Invert current value
			if ( this.open ) {
				this.removeAttribute( 'open' );
			} else {
				this.setAttribute( 'open', '' );
			}
		} );
	}

	/**
	 * Listens clicks on the options
	 *
	 * @return {undefined}
	 */
	#listenClicksOnOptions() {
		// Iterate options
		this.$options.forEach( ( $option ) => {
			// Add event listener
			$option.addEventListener( 'click', () => {
				// Select the option
				this.value = $option.value;
			} );
		} );
	}

	/**
	 * Sets a new value
	 *
	 * @param {String} newValue The value
	 * @return {undefined}
	 */
	set value( newValue ) {
		// Check if the value is valid
		if ( this.optionExists( newValue ) ) {
			// Check if the new value is different
			if ( this.value != newValue ) {
				// Unselect all options
				// Iterate all options
				this.$options.forEach( ( $option ) => {
					// Unselect option
					$option.selected = false;
				} );

				// Select the new option
				this.#selectOption( newValue );

				// Render the selected option
				this.#renderSelectedOption();

				// Trigger change event
				this.dispatchEvent(
					new Event( 'change', {
						bubbles: true,
						composed: true,
					} )
				);
			}
		}
	}

	/**
	 * Returns the value
	 *
	 * @return {String} The value
	 */
	get value() {
		// Default value
		let value = '';

		// Check if there are options
		if ( this.hasOptions ) {
			// Iterate options
			this.$options.forEach( ( $option ) => {
				// Searchs for the one selected
				if ( $option.selected ) {
					value = $option.value;
				}
			} );

			// Check if we still don't have a value
			if ( isEmpty( value ) ) {
				// Use the value of the first option
				value = this.$options[ 0 ].value;
			}
		}

		return value;
	}

	/**
	 * Determines if the select is open by checking if the element has
	 * the attribute "open"
	 *
	 * @return {Boolean} True if it's open
	 */
	get open() {
		return this.hasAttribute( 'open' );
	}

	/**
	 * Sets the status of the open attribute
	 *
	 * @param {Boolean} value True if the select should be open
	 * @return {undefined}
	 */
	set open( value ) {
		if ( value ) {
			// Set open status in the host
			this.setAttribute( 'open', '' );

			// Add open attribute to the main element
			this.$select.setAttribute( 'open', '' );

			// Listen clicks outside
			this.#listenClicksOutside();
		} else {
			// Set open status in the host
			this.removeAttribute( 'open' );

			// Remove open attribute to the main element
			this.$select.removeAttribute( 'open' );
		}
	}

	/**
	 * Checks whether the select has options
	 *
	 * @return {Boolean} True if it has options
	 */
	get hasOptions() {
		return ! isEmpty( this.$options );
	}

	/**
	 * Renders the selected option
	 *
	 * @return {undefined}
	 */
	#renderSelectedOption() {
		// Get current selected option
		const $option = this.getOptionById( this.value );

		// Add content to the button
		this.$button.innerText = $option.text;
	}

	/**
	 * Searchs an option using the value
	 *
	 * @param {String} optionValue The option value
	 * @return {Node}               The option node
	 */
	getOptionById( optionValue ) {
		// Default response
		let $response = null;

		// Iterate options
		this.$options.forEach( ( $option ) => {
			// Compare the value
			if ( $option.value == optionValue ) {
				// Exists, thus is valid
				$response = $option;
			}
		} );

		return $response;
	}

	/**
	 * Check if a value is valid by checking if the option exists
	 *
	 * @param {String} optionValue The value
	 * @return {Boolean}       True if the value is valid
	 */
	optionExists( optionValue ) {
		return isDefined( this.getOptionById( optionValue ) );
	}

	/**
	 * Select an option
	 *
	 * @param {String} optionValue The value of the option that has to be selected
	 * @return {undefined}
	 */
	#selectOption( optionValue ) {
		// Search for the option
		const $option = this.getOptionById( optionValue );

		// Check if it's defined
		if ( isDefined( $option ) ) {
			// Change the "selected" property
			$option.selected = true;

			// Close the select
			this.removeAttribute( 'open' );
		}
	}

	/**
	 * Listens clicks outside the select
	 *
	 * @return {undefined}
	 */
	#listenClicksOutside() {
		/**
		 * Closes select
		 *
		 * @param {PointerEvent} event The event
		 * @return {undefined}
		 */
		const closeModal = ( event ) => {
			// Check if the user clicked outside
			if ( ! this.contains( event.target ) ) {
				// Remove the "open" attribute
				this.removeAttribute( 'open' );

				// Remove event listener
				document.removeEventListener( 'click', closeModal );
			}
		};

		// Listen clicks
		document.addEventListener( 'click', closeModal );
	}

	/**
	 * Returns the select
	 *
	 * @return {Node} The select
	 */
	get $select() {
		return this.shadowRoot.querySelector( '.select' );
	}

	/**
	 * Returns all options inside the select
	 *
	 * @return {NodeList} The options
	 */
	get $options() {
		return this.querySelectorAll( ':scope > uo-select-option' );
	}

	/**
	 * Returns the select button (the one that opens the dropdown)
	 *
	 * @return {Node} The button
	 */
	get $button() {
		return this.shadowRoot.querySelector( '.select-button' );
	}
}

customElements.define( 'uo-select', AutomatorSelect );
