ING ===========\n\t */\n\n\t&[status],\n\t&[loading] {\n\t\ta {\n\t\t\tpadding-right: 16px + 10px;\n\t\t}\n\n\t\t&::after {\n\t\t\t@include icon;\n\t\t\tcontent: var(--uap-icon-times);\n\n\t\t\tfont-size: 15px;\n\n\t\t\tposition: absolute;\n\t\t\ttop: 50%;\n\t\t\ttransform: translateY(-50%);\n\t\t\tright: 0;\n\t\t}\n\n\t\t&[direction=\"column\"] {\n\t\t\ta {\n\t\t\t\tpadding-right: 20px + 16px + 10px;\n\t\t\t}\n\n\t\t\t&::after {\n\t\t\t\tright: 20px;\n\t\t\t}\n\t\t}\n\t}\n\n\t/**\n\t * Warning\n\t */\n\n\t&[status=\"warning\"] {\n\t\t&::after {\n\t\t\tcontent: var(--uap-icon-info-circle);\n\t\t\tcolor: var(--uap-font-color-warning);\n\t\t}\n\t}\n\n\t/**\n\t * Error\n\t */\n\n\t&[status=\"error\"] {\n\t\t&::after {\n\t\t\tcontent: var(--uap-icon-info-circle);\n\t\t\tcolor: var(--uap-font-color-error);\n\t\t}\n\t}\n\n\t/**\n\t * Success\n\t */\n\n\t&[status=\"success\"] {\n\t\t&::after {\n\t\t\tcontent: var(--uap-icon-check-circle);\n\t\t\tcolor: var(--uap-font-color-success);\n\t\t}\n\t}\n\n\t/**\n\t * =========== LOADING ===========\n\t */\n\n\t&[loading] {\n\t\t&::after {\n\t\t\t@include icon-loading( translateY( -50% ) );\n\n\t\t\t// Override left from mixin\n\t\t\tleft: auto;\n\t\t\tright: 0;\n\n\t\t\tfont-size: 15px;\n\t\t\twidth: 15px;\n\t\t\theight: 15px;\n\t\t}\n\n\t\t&[direction=\"column\"] {\n\t\t\t&::after {\n\t\t\t\tright: 25px;\n\t\t\t}\n\t\t}\n\t}\n}\n\n// Border radius\n:host([direction=\"column\"]:first-child) {\n\tborder-top-left-radius: var(--uap-border-radius);\n\n\t.tab {\n\t\tborder-top-left-radius: var(--uap-border-radius);\n\t}\n}\n\n/**\n * ============== INTEGRATION WITH OTHER COMPONENTS ==============\n */\n\n/**\n * Icon\n * <uo-icon>\n */\n\n::slotted(uo-icon:first-child) {\n\twidth: 20px;\n\tfont-size: 20px;\n\tline-height: 1;\n\n\tmargin-right: 10px;\n}\n","/**\n * This file only contains SCSS mixins to use an icon as pseudo-element\n * Don't use the unicode to add the icon, use the CSS variables, which\n * are defined in style.scss\n */\n\n@mixin icon {\n\tfont-size: 1em;\n\tfont-family: \"Uncanny Automator Icons\" !important;\n\tspeak: none;\n\tfont-style: normal;\n\tfont-weight: normal;\n\tfont-variant: normal;\n\ttext-transform: none;\n\tline-height: 1;\n\t-webkit-font-smoothing: antialiased;\n\t-moz-osx-font-smoothing: grayscale;\n}\n\n@mixin icon-loading( $translate: translate(-50%, -50%) ) {\n\t// Define the name of the keyframes animation\n\t$keyframes_name: unique_id() + \"-rotate\";\n\n\t// Add icon\n\t@include icon;\n\tcontent: var(--uap-icon-loading);\n\n\t// Style it\n\tcolor: var(--uap-font-color);\n\n\t// Center it\n\tposition: absolute;\n\ttop: 50%;\n\tleft: 50%;\n\n\t// Add animation\n\tanimation: #{ $keyframes_name } 2s linear infinite;\n\t@keyframes #{ $keyframes_name } {\n\t\tfrom {\n\t\t\ttransform: $translate rotate(0deg);\n\t\t}\n\t\tto {\n\t\t\ttransform: $translate rotate(360deg);\n\t\t}\n\t}\n}\n"],"sourceRoot":""}]);
