// Import fonts globally
import './style.scss';

// Import style for web component
const styles = require( './web-component-style.scss' );

// Icon template
const template = document.createElement( 'template' );
template.innerHTML = `
    <style>

        :host {
            display: inline-flex;
        }

        ${ styles }

    </style>

    <div class="icon"></div>
`;

/**
 * Icon <uo-icon>
 *
 * Attributes:
 * id  The ID of the icon
 */
class AutomatorIcon extends HTMLElement {
	/**
	 * Defines observed attributes
	 *
	 * @return {Array} Observed attributes
	 */
	static get observedAttributes() {
		return [ 'id' ];
	}

	/**
	 * Creates the component
	 *
	 * @return {undefined}
	 */
	constructor() {
		super();

		// Attach shadow
		this.attachShadow( {
			mode: 'open',
		} );

		// Create icon
		this.shadowRoot.appendChild( template.content.cloneNode( true ) );
	}

	/**
	 * Callback invoked when the attribute changes
	 *
	 * @param {String} attrName Attribute name
	 * @param {String} oldValue The old value
	 * @param {String} newValue The new value
	 * @return {undefined}
	 */
	attributeChangedCallback( attrName, oldValue, newValue ) {
		// Check if the old value is different than the new one
		// This will prevent an infinite loop
		if ( oldValue !== newValue ) {
			this[ attrName ] = this.getAttribute( attrName );
		}
	}

	/**
	 * Returns the id of the icon
	 *
	 * @return {String} The id
	 */
	get id() {
		return this.getAttribute( 'id' );
	}

	/**
	 * Sets the id of the icon
	 *
	 * @return {undefined}
	 */
	set id( value ) {
		// Check if the value exists
		if ( value ) {
			// Update the attribute
			this.setAttribute( 'id', value );

			// Set attribute in the icon element
			this.$icon.setAttribute( 'id', value );

			// Check if it's an integration icon
			if ( this.integrations.includes( value ) ) {
				// Set attribute in the icon element
				this.$icon.setAttribute( 'is-integration', '' );
			}
		} else {
			// Remove the attribute
			this.removeAttribute( 'id' );

			// Remove attribute from the icon element
			this.$icon.removeAttribute( 'id' );

			// Remove attribute in the icon element
			this.$icon.removeAttribute( 'is-integration' );
		}
	}

	/**
	 * Returns the icon element
	 *
	 * @return {Node} The icon
	 */
	get $icon() {
		return this.shadowRoot.querySelector( '.icon' );
	}

	/**
	 * List of supported integrations
	 *
	 * @return {Array} Integrations
	 */
	get integrations() {
		return [
			'active-campaign',
			'facebook',
			'google-sheet',
			'google',
			'hubspot',
			'instagram',
			'mailchimp',
			'slack',
			'twitter',
			'zoom',
			'twilio',
			'gotowebinar',
			'gototraining',
		];
	}
}

customElements.define( 'uo-icon', AutomatorIcon );
