,CAEA,+EAAA,CAGA,+HAQC,iFAAA,CAEA,qFAAA,CAEA,sDAAA,CAID,8BAEC,sGAAA,CASF,qBAEC,gEAAA,CAEA,8EAAA,CAGA,mHAQC,gFAAA,CAEA,oFAAA,CAEA,sDAAA,CAID,2BAEC,qGAAA,CA2BF,mBACC,WAAA,CAGA,gBAAA,CACA,iBAAA,CAQD,yBACC,WAAA,CAGA,gBAAA,CACA,iBAAA,CAEA,4CAAA,CACA,gDAAA","sourcesContent":["/**\n * Button: <uo-button>\n *\n * Overridable styles\n * This has to be used as a last resort. Try to use the default styles.\n *\n * Colors:\n * --uap-button-style-custom-background        Hex  Background color\n * --uap-button-style-custom-color             Hex  Font color\n * --uap-button-style-custom-border-color      Hex  Border color\n * --uap-button-style-custom-hover-background  Hex  Background on hover\n * --uap-button-style-custom-hover-color       Hex  Font color on hover\n * --uap-button-style-custom-shadow-rgb        RGB  Shadow on focus\n */\n\n// Import icon mixin\n@import \"@automator/components/icon/mixin.scss\";\n\n// Variables\n$focus-shadow: 0 0 0 0.2rem;\n$focus-shadow-alpha: 0.5;\n\n// Make buttons inline-flex by default\n:host {\n\tdisplay: inline-flex;\n}\n\n// Disable all pointer events if the button is disabled\n:host-context([disabled]) {\n\tpointer-events: none;\n}\n\nbutton {\n\tbackground: var(--uap-button-style-custom-background, #fff);\n\n\tborder: 1px solid transparent;\n\tborder-radius: var(--uap-border-radius);\n\n\tfont-family: var(--uap-font-family);\n\tfont-weight: 500;\n\twhite-space: nowrap;\n\n\tbox-shadow: var(--uap-box-shadow-small);\n\n\t// Cursor pointer, it's a button after all\n\tcursor: pointer;\n\n\t// Position relative because we will add an absolute element\n\t// inside for the loading animation\n\tposition: relative;\n\n\t// Transition: The first property sets the timing function and\n\t// duration. We will set the property below.\n\ttransition: var(--uap-transition);\n\ttransition-property: background, color, box-shadow;\n\n\t/**\n\t * ================ DISABLED ================\n\t */\n\n\t/**\n\t * Disabled\n\t * Figma: <Button> State: Disabled\n\t */\n\n\t&[disabled] {\n\t\topacity: 0.6;\n\t\tcursor: not-allowed;\n\t\tpointer-events: none;\n\t}\n\n\t/**\n\t * ================ LOADING ================\n\t */\n\n\t/**\n\t * Loading\n\t * Figma: <Button> State: Loading\n\t */\n\n\t&[loading] {\n\t\t// We want to hide the text, but we don't want\n\t\t// to change the size of the button, so we hide\n\t\t// it by changing the color to transparent\n\t\tcolor: transparent !important;\n\n\t\t&::before {\n\t\t\t@include icon-loading;\n\t\t\tcolor: #fff;\n\t\t}\n\t}\n\n\t/**\n\t * ================ COLORS ================\n\t */\n\n\t/**\n\t * Default / Primary\n\t * Figma: <Button> Style: Default\n\t */\n\n\t// Check if the user defined a custom property first\n\tcolor: var(--uap-button-style-custom-color, var(--uap-color-blue));\n\t// Check if the user defined a custom property first\n\tborder-color: var(--uap-button-style-custom-border-color, var(--uap-color-blue));\n\n\t// When disabled\n\t&[disabled],\n\t// When loading\n\t&[loading],\n\t// On hover\n\t&:hover,\n\t// On focus\n\t&:focus {\n\t\t// Check if the user defined a custom property first\n\t\tbackground: var(--uap-button-style-custom-hover-background, var(--uap-color-blue));\n\t\t// Check if the user defined a custom property first\n\t\tcolor: var(--uap-button-style-custom-hover-color, #fff);\n\t}\n\n\t// Add shadow when it's focused\n\t&:focus {\n\t\t// Check if the user defined a custom property first\n\t\tbox-shadow: $focus-shadow rgba(var(--uap-button-style-custom-shadow-rgb, var(--uap-color-blue-rgb)), $focus-shadow-alpha);\n\t}\n\n\t/**\n\t * Secondary\n\t * Figma: <Button> Style: Secondary\n\t */\n\n\t&[color=\"secondary\"] {\n\t\t// Check if the user defined a custom property first\n\t\tcolor: var(--uap-button-style-custom-color, var(--uap-font-color));\n\t\t// Check if the user defined a custom property first\n\t\tborder-color: var(--uap-button-style-custom-border-color, var(--uap-color-gray));\n\n\t\t// When disabled\n\t\t&[disabled],\n\t\t// When loading\n\t\t&[loading],\n\t\t// On hover\n\t\t&:hover,\n\t\t// On focus\n\t\t&:focus {\n\t\t\t// Check if the user defined a custom property first\n\t\t\tbackground: var(--uap-button-style-custom-hover-background, var(--uap-font-color));\n\t\t\t// Check if the user defined a custom property first\n\t\t\tborder-color: var(--uap-button-style-custom-hover-border-color, var(--uap-font-color));\n\t\t\t// Check if the user defined a custom property first\n\t\t\tcolor: var(--uap-button-style-custom-hover-color, #fff);\n\t\t}\n\n\t\t// Add shadow when it's focused\n\t\t&:focus {\n\t\t\t// Check if the user defined a custom property first\n\t\t\tbox-shadow: $focus-shadow rgba(var(--uap-button-style-custom-shadow-rgb, var(--uap-color-gray-rgb)), $focus-shadow-alpha);\n\t\t}\n\t}\n\n\t/**\n\t * Danger\n\t * Figma: <Button> Style: Danger\n\t */\n\n\t&[color=\"danger\"] {\n\t\t// Check if the user defined a custom property first\n\t\tcolor: var(--uap-button-style-custom-color, var(--uap-color-red));\n\t\t// Check if the user defined a custom property first\n\t\tborder-color: var(--uap-button-style-custom-border-color, var(--uap-color-red));\n\n\t\t// When disabled\n\t\t&[disabled],\n\t\t// When loading\n\t\t&[loading],\n\t\t// On hover\n\t\t&:hover,\n\t\t// On focus\n\t\t&:focus {\n\t\t\t// Check if the user defined a custom property first\n\t\t\tbackground: var(--uap-button-style-custom-hover-background, var(--uap-color-red));\n\t\t\t// Check if the user defined a custom property first\n\t\t\tborder-color: var(--uap-button-style-custom-hover-border-color, var(--uap-color-red));\n\t\t\t// Check if the user defined a custom property first\n\t\t\tcolor: var(--uap-button-style-custom-hover-color, #fff);\n\t\t}\n\n\t\t// Add shadow when it's focused\n\t\t&:focus {\n\t\t\t// Check if the user defined a custom property first\n\t\t\tbox-shadow: $focus-shadow rgba(var(--uap-button-style-custom-shadow-rgb, var(--uap-color-red-rgb)), $focus-shadow-alpha);\n\t\t}\n\t}\n\n\t/**\n\t * ================ SIZES ================\n\t */\n\n\t/**\n\t * Default\n\t * Figma: <Button> Size: Default\n\t */\n\n\tfont-size: var(--uap-font-size--body-xsmall);\n\tline-height: var(--uap-line-height--body-xsmall);\n\n\theight: 34px;\n\n\t// Padding: Just set the side paddings, the <button> will center the text in the middle\n\tpadding-left: 15px;\n\tpadding-right: 15px;\n\n\t/**\n\t * Small\n\t * Figma: <Button> Size: Small\n\t */\n\n\t&[size=\"small\"] {\n\t\theight: 26px;\n\n\t\t// Padding: Just set the side paddings, the <button> will center the text in the middle\n\t\tpadding-left: 8px;\n\t\tpadding-right: 8px;\n\t}\n\n\t/**\n\t * Extra small\n\t * Figma: <Button> Size: Extra small\n\t */\n\n\t&[size=\"extra-small\"] {\n\t\theight: 22px;\n\n\t\t// Padding: Just set the side paddings, the <button> will center the text in the middle\n\t\tpadding-left: 8px;\n\t\tpadding-right: 8px;\n\n\t\tfont-size: var(--uap-font-size--body-xxsmall);\n\t\tline-height: var(--uap-line-height--body-xxsmall);\n\t}\n}\n","/**\n * This file only contains SCSS mixins to use an icon as pseudo-element\n * Don't use the unicode to add the icon, use the CSS variables, which\n * are defined in style.scss\n */\n\n@mixin icon {\n\tfont-size: 1em;\n\tfont-family: \"Uncanny Automator Icons\" !important;\n\tspeak: none;\n\tfont-style: normal;\n\tfont-weight: normal;\n\tfont-variant: normal;\n\ttext-transform: none;\n\tline-height: 1;\n\t-webkit-font-smoothing: antialiased;\n\t-moz-osx-font-smoothing: grayscale;\n}\n\n@mixin icon-loading( $translate: translate(-50%, -50%) ) {\n\t// Define the name of the keyframes animation\n\t$keyframes_name: unique_id() + \"-rotate\";\n\n\t// Add icon\n\t@include icon;\n\tcontent: var(--uap-icon-loading);\n\n\t// Style it\n\tcolor: var(--uap-font-color);\n\n\t// Center it\n\tposition: absolute;\n\ttop: 50%;\n\tleft: 50%;\n\n\t// Add animation\n\tanimation: #{ $keyframes_name } 2s linear infinite;\n\t@keyframes #{ $keyframes_name } {\n\t\tfrom {\n\t\t\ttransform: $translate rotate(0deg);\n\t\t}\n\t\tto {\n\t\t\ttransform: $translate rotate(360deg);\n\t\t}\n\t}\n}\n"],"sourceRoot":""}]);
