// Export utilities so external scripts can use them
// First, import the ones we want to export
import addParametersToURL from './add-parameters-to-url';
import debounce from './debounce';
import getURLParameter from './get-url-parameter';
import getFormFields from './get-form-fields';
import isDefined from './is-defined';
import isEmpty from './is-empty';
import onReady from './on-ready';
import fetchData from './fetch-data';
import throttle from './throttle';
import uniqueID from './unique-id';

// Import other utilities
// Import sprintf.js
const sprintf = require( 'sprintf-js' ).sprintf;

// Export it
window._uo = isDefined( window._uo ) ? window._uo : {};
window._uo.utility = {
	addParametersToURL: addParametersToURL,
	debounce: debounce,
	getURLParameter: getURLParameter,
	getFormFields: getFormFields,
	isDefined: isDefined,
	isEmpty: isEmpty,
	onReady: onReady,
	fetchData: fetchData,
	throttle: throttle,
	uniqueID: uniqueID,
	sprintf: sprintf,
};
